<template>
    <div class="user-list">
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>

        <el-card>
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
                        <el-button slot="apppend" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="dialogVisible = true">添加用户</el-button>
                </el-col>
            </el-row>

            <el-table :data="userlist" border stripe>
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="姓名" prop="username"></el-table-column>
                <el-table-column label="电话" prop="telphone"></el-table-column>
                <el-table-column label="邮箱" prop="email"></el-table-column>
                <el-table-column label="角色" prop="permission"></el-table-column>
                <el-table-column label="状态" prop="status">
                    <template slot-scope="scope">
                        <el-switch v-model="scope.row.status" @change="userStatusChange(scope.row)"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180px">
                    <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
                    <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
                        <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                    </el-tooltip>
                </el-table-column>
            </el-table>

            <el-pagination 
                :page-sizes="[5, 10, 15, 20]" 
                :total="pagetotal" 
                style="text-align: right;"
                :current-page.sync="queryInfo.pagenum"
                :page-size.sync="queryInfo.pagesize"
                layout="total, prev, pager, next, sizes"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
            >
            </el-pagination>
        </el-card>

        <el-dialog title="添加用户" :visible.sync="dialogVisible" width="50%" @close="dialogClose">
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="addForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addForm.password"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="telphone">
                    <el-input v-model="addForm.telphone"></el-input>
                </el-form-item>
                <el-form-item label="email" prop="email">
                    <el-input v-model="addForm.email"></el-input>
                </el-form-item>
                <span>
                    <el-button @click="dialogVisible = false">取消</el-button>
                    <el-button type="primary" @click="addUser">确定</el-button>
                </span>
            </el-form>
        </el-dialog>
    </div>
</template>

<style lang="scss" scoped>
    .el-table {
        margin-top: 20px;
    }
    .el-dialog {
        .el-form {
            span {
                display: flex;
                justify-content: space-evenly;
            }
        }
    }
</style>

<script>
import { validateUsername, validateTelphone, validatePassword, validateEmail } from '@/tools/validator'
export default {
    data() {
        return {
            // 搜索信息
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 2
            },
            userlist: [],
            pagetotal: 0,
            // 添加用户对话框显示与隐藏
            dialogVisible: false,
            addForm: {
                username: '',
                password: '',
                telphone: '',
                email: ''
            },
            addFormRules: {
                username: [{ required: true, trigger: 'blur', validator: validateUsername, message: '请输入4-10位用户名'}],
                password: [{ required: true, trigger: 'blur', validator: validatePassword, message: '请输入4-10位密码'}],
                telphone: [{ required: true, trigger: 'blur', validator: validateTelphone, message: '请输入合法手机号'}],
                email: [{ required: true, trigger: 'blur', validator: validateEmail, message: '请输入合法邮箱'}],
            }
        }
    },
    methods: {
        async getUserList() {
            // 根据搜索信息获取数据
            const res = await this.$http.get('/data/user/userlist', { params: this.queryInfo })
            if (res.status !== 200) {
                return this.$message.error('获取用户列表失败')
            }
            this.userlist = res.data;
            this.pagetotal = res.data.length;
        },
        handleSizeChange(newpagesize) {
            this.queryInfo.pagesize = newpagesize;
            this.getUserList();
        },
        handleCurrentChange(newpagenum) {
            this.queryInfo.pagenum = newpagenum;
            this.getUserList();
        },
        async userStatusChange(userinfo) {
            // put请求通常是修改请求
            const res = await this.$http.put(`user/${userinfo.id}/status/${userinfo.status}`)
            if (res.status !== 200) {
                userinfo.status = !userinfo.status
                return this.$message.error('获取用户列表失败')
            }
                this.$message.success('获取用户列表成功')
        },
        dialogClose() {
            this.$refs.addFormRef.resetFields();
        },
        addUser() {
            this.$refs.addFormRef.validate(async valid => {
                if (valid) {
                    const res = await this.$http.post('/admin/user/createUser', this.queryInfo)
                    if (res.status !== 200) {
                        return this.$message.error('用户创建')
                    }
                    this.$message.success('创建用户成功');
                    this.dialogVisible = false;
                    this.getUserList();
                }
            })
        }
    },
    created() {
        this.getUserList();
    }
}
</script>